import React from "react"
import { Row, Col } from "reactstrap"
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb"
import ListViewConfig from "./DataListConfig"
import queryString from "query-string"
import "../../../assets/scss/plugins/forms/react-select/_react-select.scss"

class ListTopicsPage extends React.Component {
  render() {
    return (
      <React.Fragment>
        <Breadcrumbs
          breadCrumbTitle="Topic & Assessment"
          breadCrumbParent="Data list"
          breadCrumbActive="Topic & Assessment"
        />
        <Row>
          <Col sm="12">
            <ListViewConfig parsedFilter={queryString.parse(this.props.location.search)}/>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}

export default ListTopicsPage