import React, { Component } from "react"
import { Label, Input, FormGroup, Button,  } from "reactstrap"
import { X } from "react-feather"
import PerfectScrollbar from "react-perfect-scrollbar"
import classnames from "classnames"
import { getInitialData as getLevels } from "../../../redux/actions/level"
import { getParentData as getCategories } from "../../../redux/actions/category"
import { connect } from "react-redux"
import Select from 'react-select';

const typesOptions = [
  {
    label: "Lesson",
    value: "LESSON"
  },
  {
    label: "Assessment",
    value: "ASSESSMENT"
  }
];


class DataListSidebar extends Component {
  state = {
    _id: "",
    name: "",
    description: "description",
    levelId: "",
    categoryId: "",
    type:"LESSON",
  }

  addNew = false

  componentWillMount() {
    this.props.getLevels();
    this.props.getCategories();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.data !== null && prevProps.data === null) {
      if (this.props.data._id !== prevState._id) {
        this.setState({ _id: this.props.data._id })
      }
      if (this.props.data.name !== prevState.name) {
        this.setState({ name: this.props.data.name })
      }
      if (this.props.data.categoryId !== prevState.categoryId) {
        this.setState({ categoryId: this.props.data.categoryId && this.props.data.categoryId._id })
      }
      if (this.props.data.type !== prevState.type) {
        this.setState({ type: this.props.data.type })
      }
      if (this.props.data.levelId !== prevState.levelId) {
        this.setState({ levelId: this.props.data.levelId && this.props.data.levelId._id })
      }
      if (this.props.data.description !== prevState.description) {
        this.setState({ description: this.props.data.description })
      }
    }
    if (this.props.data === null && prevProps.data !== null) {
      this.setState({
        _id: "",
        name: "",
        description: "description",
        levelId: "",
        categoryId: "",
        type:"LESSON",
      })
    }
    if (this.addNew) {
      this.setState({
        _id: "",
        name: "",
        description: "description",
        levelId: "",
        categoryId: "",
        type:"LESSON",
      })
    }
    this.addNew = false
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if(nextProps.categories.length > 0) {
      this.setState({
        categoryId: nextProps.categories[0]._id
      })
    }
    if(nextProps.levels.length > 0) {
      this.setState({
        levelId: nextProps.levels[0]._id
      })
    }
  }

  handleSubmit = obj => {
    if (this.props.data !== null) {
      this.props.updateData(obj)
    } else {
      this.addNew = true
      this.props.addData(obj)
    }
    // let params = Object.keys(this.props.dataParams).length
    //   ? this.props.dataParams
    //   : { page: 1, perPage: 4 }
    this.props.handleSidebar(false, true)
  }

  renderCategories = (categories) => {
    let renderedCategories = [];
    if(categories === null || categories === undefined || categories.length === 0) {
      return false
    }
    categories.map((category, index) => {
      renderedCategories.push({
        value: category._id, 
        label: category.name,
        // isDisabled: category.isParent
      });
      if(category.children && category.children.length > 0) {
        category.children.map((child) => {
          return renderedCategories.push({
            value: child._id,
            label: <span className='pl-1'>{child.name}</span>
          }); 
        })
      }
      return true
    });
    return renderedCategories;
  }

  renderLevel = (levels) => {
    let result = [];

    levels.map((level) => {
      return result.push({
        label: level.name,
        value: level._id
      })
    })
    return result
  }

  render() {
    let { show, handleSidebar, data } = this.props
    let { name, description, levelId, type, categoryId } = this.state;

    let categoryOptions = this.renderCategories(this.props.categories);
    let levelOptions = this.renderLevel(this.props.levels);

    let categoryDefaultValue = categoryOptions.length > 0 && categoryOptions[0]
    let typeDefaultValue = typesOptions[0]
    let levelDefaultValue =  levelOptions.length > 0 && levelOptions[0]

    if(levelId) {
      levelDefaultValue = levelOptions.find((elt) => elt.value === levelId);
    }
    if(type) {
      typeDefaultValue = typesOptions.find((elt) => elt.value === type);
    }
    if(categoryId) {
      categoryDefaultValue = categoryOptions.find((elt) => elt.value === categoryId);
    }

    return (
      <div
        className={classnames("data-list-sidebar", {
          show: show
        })}>
        <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
          <h4>{data !== null ? "UPDATE DATA" : "ADD NEW DATA"}</h4>
          <X size={20} onClick={() => handleSidebar(false, true)} />
        </div>
        <PerfectScrollbar
          className="data-list-fields px-2 mt-3"
          options={{ wheelPropagation: false }}>
          <FormGroup>
            <Label for="topic_name">Name</Label>
            <Input
              type="text"
              value={name}
              invalid={name === ""}
              placeholder="Topic name"
              onChange={e => this.setState({ name: e.target.value })}
              id="topic_name"
            />
          </FormGroup>
          <FormGroup>
            <Label for="description">Description</Label>
            <div className="form-label-group mt-2 mb-0">
              <Input
                type="textarea"
                name="text"
                id="description"
                rows="3"
                value={description}
                placeholder="Tell Others in more detail for this topic"
                onChange={e => this.setState({ description: e.target.value })}
              />
            </div>
            <small
              className={`counter-value float-right ${
                this.state.description.length > 50 ? "bg-danger" : ""
              }`}
            >
              {`${this.state.description.length}/50`}
            </small>
          </FormGroup>
          <FormGroup>
            <Label for="data-category">Category</Label>
            <Select
              id='data-category'
              className="React"
              classNamePrefix="select"
              defaultValue={categoryDefaultValue}
              value={categoryDefaultValue}  
              onChange={e => this.setState({ categoryId: e.value })}
              name="color"
              options={categoryOptions}
            />
          </FormGroup>
          <FormGroup>
            <Label for="select-type">Type</Label>
            <Select
              id='select-type'
              className="React"
              classNamePrefix="select"
              name="type"
              defaultValue={typeDefaultValue}
              value={typeDefaultValue}
              onChange={e => this.setState({ type: e.value })}
              options={typesOptions}
            />
          </FormGroup>
          <FormGroup>
            <Label for="data-level">Level</Label>
            <Select
              id="data-level" 
              className="React"
              classNamePrefix="select"
              name="type"
              defaultValue={levelDefaultValue}
              value={levelDefaultValue}
              onChange={e => this.setState({ levelId: e.value })}
              options={levelOptions}
            />
          </FormGroup>
        </PerfectScrollbar>
        <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
          <Button color="primary" onClick={() => this.handleSubmit(this.state)}>
            {data !== null ? "Update" : "Submit"}
          </Button>
          <Button
            className="ml-1"
            color="danger"
            outline
            onClick={() => handleSidebar(false, true)}>
            Cancel
          </Button>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    categories: state.category.allData,
    levels: state.level.allData
  }
}

const mapDispatchToProps = {
  getLevels,
  getCategories
}

export default connect(mapStateToProps, mapDispatchToProps)(DataListSidebar)

